import React, { Component } from 'react';
import { findDOMNode } from 'react-dom';
import EventListener from 'react-helpful/EventListener';
import Page from '../components/Page';
import AdRect from '../components/Ad/AdRect';

class Eye extends Component {

  state = {
    offsetLeft: 0,
    offsetTop: 0,
  };

  componentDidMount() {
    const node = findDOMNode(this.eye);

    this.setState({
      offsetLeft: node.offsetLeft,
      offsetTop: node.offsetTop,
    });
  }

  render() {
    const { x, y, width, height } = this.props;
    const { offsetLeft, offsetTop } = this.state;
    const x0 = offsetLeft + width / 2;
    const y0 = offsetTop + height / 2;

    const dx = x - x0;
    const dy = y0 - y;

    const l = Math.sqrt(dx * dx + dy * dy);

    const vx = 30 * dx / l;
    const vy = 30 * dy / l;

    return (
      <div
        className="eye"
        ref={eye => this.eye = eye}
        style={{
          width: width,
          height: height,
        }}
      >
        <div
          className="eye__ball"
          style={{
          width: width/2,
          height: height/2,
          left: vx,
          top: -vy,
        }} />
      </div>
    );
  }
}

export default class NotFound extends Component {

  state = {
    x: 0,
    y: 0,
  };

  onMouseMove = event => {
    this.setState({
      x: event.x,
      y: event.y,
    });
  };

  componentDidMount() {

  }

  render() {
    const { x, y } = this.state;

    return (
      <Page
        title="404"
        description="Страница не найдена"
      >
        <div className="not-found">
          <div className="not-found__title">404</div>
          <div className="eyes">
            <Eye x={x} y={y} width={128} height={128} />
            <Eye x={x} y={y} width={128} height={128} />
          </div>
          <AdRect slot="3696929149" />
        </div>

        <EventListener
          target={() => document}
          on={this.onMouseMove}
          event="mousemove"
        />

      </Page>
    );
  }
}



